html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  @include font;

  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Poppins", "Arial", sans-serif;
  color: $gray;
}

.body-black {
  position: relative;

  &::after {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    content: "";
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8);
  }
}

.body-white {
  position: relative;

  &::after {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 98;
    content: "";
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(255, 255, 255, 0);
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

// form {
//   display: flex;
//   justify-content: space-between;
//   flex-wrap: wrap;
