input {
  @include mapa;
  @include font(16px, 22px, 400);

  position: relative;
  border-radius: 21px;
  border: none;
  color: $black;
  background-color: rgba($white, 0);
  transition: 0.5s;
  padding: 12px 30px;

  &::placeholder {
    color: $gray;
    @include font(14px, 21px, 500);
  }

  &:-moz-submit-invalid {
    box-shadow: none;
  }

  &:-moz-ui-invalid {
    box-shadow: none;
  }
}

