    max-width: 768px;
  }

  @media (max-width: $mobile-width-only) {
    width: 320px;
    // max-width: 400px;
    padding: 0 15px;
  }
}





.new__wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}

.new p {
  @include font(16px, 20px, 500);

  // font-family: "Cormorant Garamond", "Times New Roman", serif;
  text-align: center;
  width: 100%;
  margin: 0;
  color: $black;

  &:first-child {
    @include font(34px, 44px, 700);
    font-family: "Cormorant Garamond", "Times New Roman", serif;
  }
}





