    .swiper-pagination-bullet {
      width: 20px;
      height: 20px;
      text-align: center;
      line-height: 20px;
      font-size: 12px;
      color: #000;
      opacity: 1;
      background: rgba(0, 0, 0, 0);
    }

    .swiper-pagination-bullet-active {
      color: #fff;
      background: #007aff;
    }

    .swiper-button-nex,
    .swiper-button-pre {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 100;
      width: 17px;
      height: 17px;
     background-image: url("../img/icon-close.svg");
    background-repeat: no-repeat;
    background-size: cover;
    }

     .swiper-button-pre {
      right: auto;
      left: 0;
       background-image: url("../img/icon-close.svg");
     }

.swiper-container button {
  background: none;
  // border: none;
  outline: none;
}


    .swiper-container {
      width: 100%;
      height: 100%;
    }

    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background: #fff;

      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
    }
